.date-icon {
  pointer-events: none;
  width: 15px;
  height: 15px;
  margin-left: 8px;
  background-image: url('../images/scroll-down-black.svg');
  background-repeat: no-repeat;
  background-position: center;
  // transform-origin: center center; // чи потрібно?
  transition: transform var(--transition);
}

body.dark-theme .date-icon {
  background-image: url('../images/scroll-down-white.svg');
}

body.dark-theme .date-icon {
  background-image: url('../images/scroll-down-white.svg');
}

.rotated {
  transform: rotate(180deg);
}
